.dashboard {
  font-family: 'Roboto Condensed', sans-serif;
  padding: 20px;
  background-color: #f9f9f9;
}

.card-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  gap: 20px;
}

.card {
  display: flex;
  align-items: center;
  padding: 20px;
  color: #fff;
  border-radius: 1px;
  flex: 1;
  min-width: 200px;
  gap: 15px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.card i {
  font-size: 36px;
}

.card h2 {
  font-size: 30px;
  margin: 0;
}

.card p {
  margin: 0;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: bold;
}

.card.users { background-color: #4caf50; }
.card.posts { background-color: #f44336; }
.card.tweets { background-color: #00bcd4; }
.card.shares { background-color: #ffc107; }

.main-section {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
}

.table-section {
  flex: 1;
  min-width: 400px;
  background: white;
  padding: 20px;
  border-radius: 1px;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
}

.employee-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.employee-table th,
.employee-table td {
  padding: 10px 15px;
  border: 1px solid #ddd;
  font-size: 14px;
}

.employee-table th {
  background-color: #ffffff;
  font-weight: 600;
}

.chart-section {
  flex: 1;
  min-width: 400px;
  background: white;
  padding: 20px;
  border-radius: 1px;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
  text-align: center;
}