.trip-claim-container {
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f8fafc, #e2e8f0);
  font-family: 'Roboto Condensed', sans-serif;
}

.trip-claim-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
}

.trip-claim-title {
  font-size: 1.4rem;
  color: #1e293b;
  font-weight: 600;
  display: flex;
  align-items: center;
}

.trip-claim-stats {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.trip-stat-card {
  background: white;
  padding: 1rem;
  border-radius: 1px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  font-weight: bold;
  color: #334155;
  flex: 1;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.trip-claim-body {
  display: flex;
  gap: 1rem;
  background: white;
  padding: 1rem;
  border-radius: 1px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}

.trip-list {
  width: 400px;
  background: #f8fafc;
  border-right: 1px solid #e2e8f0;
  padding: 1rem;
  border-radius: 1px;
  height: calc(100vh - 300px);
  overflow-y: auto;
}

.trip-list h3 {
  font-size: 16px;
  margin-bottom: 1rem;
  color: #1e293b;
  font-weight: 600;
}

.trip-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.trip-list li {
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 1px;
  padding: 10px;
  margin-bottom: 8px;
  cursor: pointer;
  position: relative;
  transition: all 0.2s ease;
}

.trip-list li:hover {
  background-color: #f1f5f9;
}

.trip-list li.active {
  background-color: #3b82f6;
  color: white;
  font-weight: bold;
}

.trip-actions {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  gap: 6px;
}

.trip-btn {
  padding: 10px 10px;
  font-size: 12px;
  font-weight: bold;
  border: none;
  border-radius: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.trip-btn.accept {
  background-color: #22c55e;
  color: white;
}

.trip-btn.reject {
  background-color: #ef4444;
  color: white;
}

.trip-map-container {
  flex: 1;
  height: 500px;
  position: relative;
  border: 1px solid #e2e8f0;
  border-radius: 1px;
  overflow: hidden;
}

.trip-map {
  width: 100%;
  height: 100%;
}

.trip-controls {
  position: absolute;
  top: 16px;
  right: 20px;
  background: white;
  padding: 6px 14px;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  gap: 10px;
  z-index: 999;
}

.trip-controls button {
  background-color: #3b82f6;
  color: white;
  border: none;
  padding: 4px 10px;
  border-radius: 1px;
  font-weight: bold;
  cursor: pointer;
}

.trip-placeholder {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #475569;
  font-weight: 500;
  font-size: 16px;
}

.speed-select {
  padding: 4px 8px;
  font-size: 13px;
  border-radius: 1px;
  border: 1px solid #cbd5e1;
  background-color: white;
  cursor: pointer;
}

.trip-location-display {
  position: absolute;
  bottom: 10px;
  left: 20px;
  z-index: 998;
  background: white;
  padding: 6px 12px;
  font-size: 14px;
  border-radius: 1px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
  color: #1e293b;
  font-weight: 500;
}
.trip-location-info {
  background: #f1f5f9;
  padding: 10px 14px;
  border-top: 1px solid #e2e8f0;
  font-size: 14px;
  color: #334155;
  line-height: 1.6;
}

.trip-controls select {
  padding: 4px 8px;
  border-radius: 1px;
  border: 1px solid #cbd5e1;
  font-weight: bold;
  font-size: 13px;
  background-color: white;
  cursor: pointer;
}