ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: white;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: linear-gradient(135deg, #b866d9, #6f2eff);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  height: 70px;
  z-index: 999;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4);
  transform: perspective(1000px) translateZ(0);
}

.navbar h1 {
  font-size: 1.8rem;
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 0;
  white-space: nowrap;
  text-shadow: 1px 1px 2px black;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-wrap: nowrap;
  font-weight: bold;
}

.nav-links li {
  position: relative;
}

.nav-links li a {
  font-size: 1rem;
  padding: 6px 8px;
  display: inline-block;
  border: 1px solid transparent;
  border-radius: 1px;
  transition: 0.3s;
  white-space: nowrap;
  cursor: pointer;
}

.nav-links li a:hover,
.nav-links li a.active {
  border: 1px solid white;
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #333;
  min-width: 180px;
  box-sizing: border-box;
  border-radius: 1px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  z-index: 1001;
  padding: 6px 0;
}

.dropdown-menu li {
  width: 100%;
}

.dropdown-menu li a,
.dropdown-menu li button {
  color: white;
  padding: 10px 10px;
  display: block;
  width: 100%;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  font-size: 0.95rem;
  box-sizing: border-box;
  font-weight: bold;
  font-family: 'Roboto Condensed', sans-serif;
}

.dropdown-menu li a:hover,
.dropdown-menu li button:hover {
  background-color: #555;
}

.dropdown-menu.show {
  display: block;
}

.navbar button {
  background-color: red;
  color: white;
  border: none;
  padding: 8px 14px;
  font-size: 0.9rem;
  cursor: pointer;
  border-radius: 1px;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-weight: bold;
}

.navbar button:hover {
  background-color: #cc0000;
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
    padding: 10px;
  }

  .nav-links {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    gap: 10px;
  }

  .dropdown-menu {
    position: static;
    width: 100%;
    box-shadow: none;
    border-radius: 0;
    padding: 0;
  }
}