.advance-container {
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f8fafc, #e2e8f0);
  font-family: 'Roboto Condensed', sans-serif;
}

.advance-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
}

.advance-title {
  font-size: 1.4rem;
  color: #1e293b;
  font-weight: 600;
  display: flex;
  align-items: center;
}

.advance-add-btn {
  background: linear-gradient(to bottom, #3b82f6, #2563eb);
  color: white;
  padding: 0.8rem 1.2rem;
  border-radius: 1px;
  border: 1px solid #1e40af;
  box-shadow: 0 4px 0 #1e3a8a, 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  transition: all 0.15s ease-in-out;
}

.advance-add-btn:active {
  transform: translateY(2px);
  box-shadow: 0 2px 0 #1e3a8a, 0 2px 4px rgba(0, 0, 0, 0.2);
}

.advance-add-btn:hover {
  background-color: #2563eb;
}

.advance-stats {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 2rem;
}

.advance-stat-card {
  flex: 1 1 200px;
  background: white;
  border-radius: 1px;
  padding: 1rem;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: bold;
  color: #334155;
}

.advance-table-container {
  overflow-x: auto;
}

.advance-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 1px;
  overflow: hidden;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}

.advance-table th,
.advance-table td {
  padding: 0.9rem 1rem;
  text-align: left;
  border-bottom: 1px solid #e2e8f0;
  font-size: 0.95rem;
}

.advance-table th {
  background-color: #cad2da;
  font-weight: 600;
  color: #475569;
}

.advance-table td {
  color: #1e293b;
}

.advance-table tr:hover {
  background-color: #f8fafc;
}

.advance-action-buttons {
  display: flex;
  gap: 0.5rem;
}

.advance-btn {
  border: none;
  padding: 6px 10px;
  border-radius: 1px;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.advance-btn.approve {
  background-color: #22c55e;
  color: white;
}

.advance-btn.reject {
  background-color: #ef4444;
  color: white;
}

.advance-btn.approve:hover {
  background-color: #16a34a;
}

.advance-btn.reject:hover {
  background-color: #dc2626;
}