.trip-list-container {
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
}

.trip-list-container h2 {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 22px;
  margin-bottom: 20px;
}

.table-scroll-container {
  overflow-x: auto;
}

.trip-photo {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 4px;
}

.action-buttons {
  display: flex;
  gap: 8px;
}

.btn {
  padding: 6px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.btn.edit {
  background-color: #4caf50;
  color: white;
}

.btn.delete {
  background-color: #f44336;
  color: white;
}

.badge {
  display: inline-block;
  margin-right: 6px;
  padding: 4px 8px;
  font-size: 12px;
  border-radius: 12px;
  color: white;
}

.applied {
  background-color: #ff9800;
}

.approved {
  background-color: #4caf50;
}

.unapproved {
  background-color: #9e9e9e;
}

.paid {
  background-color: #2196f3;
}