.leave-container {
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f8fafc, #e2e8f0);
  font-family: 'Roboto Condensed', sans-serif;
}

.leave-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
}

.leave-title {
  font-size: 1.4rem;
  color: #1e293b;
  font-weight: 600;
  display: flex;
  align-items: center;
}

.leave-stat-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 2rem;
}

.leave-stat-card {
  flex: 1 1 200px;
  background: white;
  border-radius: 1px;
  padding: 1rem;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: bold;
  color: #334155;
}

.leave-table-container {
  overflow-x: auto;
}

.leave-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 1px;
  overflow: hidden;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}

.leave-table th,
.leave-table td {
  padding: 0.9rem 1rem;
  text-align: left;
  border-bottom: 1px solid #e2e8f0;
  font-size: 0.95rem;
}

.leave-table th {
  background-color: #cad2da;
  font-weight: 600;
  color: #475569;
}

.leave-table td {
  color: #1e293b;
}

.leave-table tr:hover {
  background-color: #f8fafc;
}

.leave-action-buttons {
  display: flex;
  gap: 0.5rem;
}

.leave-btn {
  border: none;
  padding: 0.4rem 0.6rem;
  border-radius: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.leave-btn.approve {
  background-color: #22c55e;
  color: white;
}

.leave-btn.approve:hover {
  background-color: #198754;
}

.leave-btn.reject {
  background-color: #ef4444;
  color: white;
}

.leave-btn.reject:hover {
  background-color: #dc2626;
}