.login-container {
  height: 100vh;
  background-color: #e8ebf0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family:'Roboto Condensed', sans-serif;
  padding: 0 20px;
  box-sizing: border-box;
}

.login-box {
  background: white;
  border-radius: 1px;
  padding: 40px 30px;
  width: 100%;
  max-width: 360px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  text-align: center;
  box-sizing: border-box;
}

.avatar-circle {
  background: linear-gradient(135deg, #9f4db5, #5c00e6);
  width: 80px;
  height: 80px;
  margin: 0 auto 15px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 35px;
}

.login-title {
  font-size: 24px;
  margin-bottom: 30px;
  font-weight: 600;
  color: #333;
  font-family:'Roboto Condensed', sans-serif;
}

.input-group {
  position: relative;
  margin-bottom: 20px;
  width: 100%;
  box-sizing: border-box;
  font-family:'Roboto Condensed', sans-serif;
}

.input-group .icon {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
  color: #888;
  font-size: 14px;
}

.input-group input {
  width: 100%;
  padding: 12px 12px 12px 38px;
  border: 1px solid #ccc;
  border-radius: 1px;
  font-size: 14px;
  outline: none;
  transition: border 0.3s;
  box-sizing: border-box;
  font-family:'Roboto Condensed', sans-serif;
  font-weight: bold;
}

.input-group input:focus {
  border-color: #5c00e6;
  font-family:'Roboto Condensed', sans-serif;
  font-weight: bold;
}

.login-button {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 1px;
  background: linear-gradient(135deg, #9f4db5, #5c00e6);
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  margin-top: 10px;
  transition: background 0.3s;
  font-family:'Roboto Condensed', sans-serif;
}

.login-button:hover {
  background: linear-gradient(135deg, #b866d9, #6f2eff);
}

.bottom-links {
  margin-top: 15px;
  font-size: 13px;
  color: #555;
  font-family:'Roboto Condensed', sans-serif;
  font-weight: bold;
}

.bottom-links a {
  color: #5c00e6;
  text-decoration: none;
  margin: 0 3px;
  font-family:'Roboto Condensed', sans-serif;
  font-weight: bold;
}

.bottom-links a:hover {
  text-decoration: underline;
}