.employee-container {
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f8fafc, #e2e8f0);
  font-family: 'Roboto Condensed', sans-serif;
}

.employee-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.employee-title {
  font-size: 1.4rem;
  color: #1e293b;
  font-weight: 600;
  display: flex;
  align-items: center;
}

.employee-actions {
  display: flex;
  gap: 1rem;
  align-items: center;
  flex-wrap: wrap;
}

.month-label {
  font-weight: 500;
  color: #1e293b;
  font-size: 14px;
  font-weight: bold;
}

.month-label select {
  margin-left: 6px;
  height: 36px;
  padding: 0 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 1px;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
}

.employee-add-btn {
  background: linear-gradient(to bottom, #3b82f6, #2563eb);
  color: white;
  height: 36px;
  padding: 0 16px;
  border-radius: 1px;
  border: 1px solid #1e40af;
  box-shadow: 0 4px 0 #1e3a8a, 0 4px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  font-size: 14px;
  transition: all 0.15s ease-in-out;
  display: flex;
  align-items: center;
}

.employee-add-btn:active {
  transform: translateY(2px);
  box-shadow: 0 2px 0 #1e3a8a, 0 2px 4px rgba(0, 0, 0, 0.2);
}

.employee-add-btn:hover {
  background-color: #2563eb;
}

.employee-stats {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.employee-stat-card {
  flex: 1 1 200px;
  background: white;
  border-radius: 1px;
  padding: 1rem;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: bold;
  color: #334155;
}

.employee-table-container {
  overflow-x: auto;
  background: white;
  padding: 1rem;
  border-radius: 1px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}

.employee-table {
  border-collapse: collapse;
  width: max-content;
  min-width: 100%;
}

.employee-table th,
.employee-table td {
  border: 1px solid #e2e8f0;
  padding: 8px 10px;
  text-align: center;
  font-size: 13.5px;
  white-space: nowrap;
}

.employee-table th {
  background-color: #cad2da;
  font-weight: 600;
  color: #475569;
}

.employee-table td {
  color: #1e293b;
}

.employee-table tr:hover {
  background-color: #f8fafc;
}

.cell-s {
  background-color: #facc15;
  font-weight: bold;
}

.cell-h {
  background-color: #f9a8d4;
  font-weight: bold;
}