.workshift-container {
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f8fafc, #e2e8f0);
  font-family: 'Roboto Condensed', sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
}

.add-btn {
  background: linear-gradient(to bottom, #3b82f6, #2563eb);
  color: white;
  padding: 0.8rem 1.2rem;
  border-radius: 1px;
  border: 1px solid #1e40af;
  box-shadow: 0 4px 0 #1e3a8a, 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  transition: all 0.15s ease-in-out;
}

.add-btn:active {
  transform: translateY(2px);
  box-shadow: 0 2px 0 #1e3a8a, 0 2px 4px rgba(0, 0, 0, 0.2);
}

.add-btn:hover {
  background-color: #2563eb;
}

.stats {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 2rem;
}

.stat-card {
  flex: 1 1 200px;
  background: white;
  border-radius: 1px;
  padding: 1rem;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: bold;
  color: #334155;
}

.stat-card svg {
  color: #0f172a;
}

.shift-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 1px;
  overflow: hidden;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}

.shift-table th,
.shift-table td {
  padding: 0.9rem 1rem;
  text-align: left;
  border-bottom: 1px solid #e2e8f0;
  font-size: 0.95rem;
}

.shift-table th {
  background-color: #cad2da;
  font-weight: 600;
  color: #475569;
}

.shift-table td {
  color: #1e293b;
}

.shift-table tr:hover {
  background-color: #f8fafc;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal-content {
  background: white;
  padding: 2rem;
  border-radius: 1px;
  width: 90%;
  max-width: 480px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.modal-content h2 {
  margin-bottom: 1.5rem;
  font-size: 1.4rem;
  color: #1e293b;
  font-weight: 600;
}

.modal-content label {
  display: block;
  margin-bottom: 0.25rem;
  font-weight: bold;
  color: #475569;
}

.modal-content input {
  width: 100%;
  padding: 0.6rem 0.75rem;
  border-radius: 1px;
  border: 1px solid #cbd5e1;
  margin-bottom: 1rem;
  font-size: 0.95rem;
}

.modal-content form {
  width: 93%;
}

.modal-content input:focus {
  outline: none;
  border-color: #3b82f6;
}

.modal-actions {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.modal-actions button {
  flex: 1;
  padding: 0.6rem 0.75rem;
  font-weight: 500;
  border: none;
  border-radius: 1px;
  cursor: pointer;
  font-weight: bold;
}

.modal-actions button[type='submit'] {
  background-color: #3b82f6;
  color: white;
}

.modal-actions button[type='submit']:hover {
  background-color: #2563eb;
}

.modal-actions button[type='button'] {
  background-color: #e2e8f0;
  color: #1e293b;
}

.modal-actions button[type='button']:hover {
  background-color: #cbd5e1;
}

.loading,
.no-data {
  text-align: center;
  padding: 2rem;
  background: white;
  border-radius: 1px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  color: #475569;
  font-weight: 500;
}

.action-buttons {
  display: flex;
  gap: 0.5rem;
}

.edit-btn {
  background-color: #22c55e;
  color: white;
  border: none;
  padding: 0.4rem 0.6rem;
  border-radius: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.edit-btn:hover {
  background-color: #dc2626;
}

.delete-btn {
  background-color: #ef4444;
  color: white;
  border: none;
  padding: 0.4rem 0.6rem;
  border-radius: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.delete-btn:hover {
  background-color: #16a34a;
}