.add-employee-wrapper {
  padding: 2rem;
  background: linear-gradient(to bottom right, #f8fafc, #e2e8f0);
  min-height: 100vh;
  font-family: 'Roboto Condensed', sans-serif;
}

.add-employee-inner {
  max-width: 1300px;
  margin: 0 auto;
}

.add-employee-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
}

.add-employee-title {
  font-size: 22px;
  font-weight: bold;
  color: #1f2937;
}

.add-employee-list-btn {
  background: linear-gradient(to bottom, #3b82f6, #2563eb);
  color: white;
  padding: 0.8rem 1.2rem;
  border-radius: 1px;
  border: 1px solid #1e40af;
  box-shadow: 0 4px 0 #1e3a8a, 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  transition: all 0.15s ease-in-out;
}

.add-employee-list-btn:active {
  transform: translateY(2px);
  box-shadow: 0 2px 0 #1e3a8a, 0 2px 4px rgba(0, 0, 0, 0.2);
}

.add-employee-list-btn:hover {
  background-color: #2563eb;
}

.add-employee-form-container {
  padding: 30px;
  background: white;
  border-radius: 1px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.05);
  margin-bottom: 50px;
}

.add-employee-form-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 20px;
}

.add-employee-form-item {
  flex: 1 1 calc(25% - 15px);
  display: flex;
  flex-direction: column;
  min-width: 200px;
}

.add-employee-form-item label {
  margin-bottom: 6px;
  font-weight: bold;
  color: #334155;
  font-size: 13px;
}

.add-employee-form-item input {
  padding: 14px 10px;
  font-size: 14px;
  border: 1px solid #cbd5e1;
  border-radius: 1px;
  background-color: #f8fafc;
  transition: border-color 0.2s;
  font-family: 'Roboto', sans-serif;
}

.add-employee-form-item input:focus {
  border-color: #2563eb;
  outline: none;
  background-color: white;
}

.add-employee-form-footer {
  text-align: right;
  margin: 30px 0 0;
}

.add-employee-form-footer button {
  padding: 10px 30px;
  background: linear-gradient(to bottom, #3b82f6, #2563eb);
  color: white;
  border: none;
  border-radius: 1px;
  font-size: 16px;
  cursor: pointer;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  box-shadow: 0 4px 0 #1e3a8a, 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.15s ease-in-out;
}

.add-employee-form-footer button:hover {
  background-color: #2563eb;
}

.add-employee-form-footer button:active {
  transform: translateY(2px);
  box-shadow: 0 2px 0 #1e3a8a, 0 2px 4px rgba(0, 0, 0, 0.2);
}

.file-input-wrapper input[type="file"] {
  padding: 6px;
  background: none;
  border: none;
  border-radius: 0;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
}

.file-input-wrapper input[type="file"]::-webkit-file-upload-button {
  background: #3b82f6;
  border: none;
  color: white;
  padding: 8px 12px;
  border-radius: 1px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s;
}

.file-input-wrapper input[type="file"]::-webkit-file-upload-button:hover {
  background: #2563eb;
}

.react-datepicker-custom-input {
  padding: 14px 10px;
  font-size: 14px;
  border: 1px solid #cbd5e1;
  border-radius: 1px;
  background-color: #f8fafc;
  transition: border-color 0.2s;
  width: 100%;
}

.datepicker-input {
  padding: 14px 10px;
  font-size: 14px;
  border: 1px solid #cbd5e1;
  border-radius: 1px;
  background-color: #f8fafc;
  transition: border-color 0.2s;
  width: 100%;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

.datepicker-input:focus {
  border-color: #2563eb;
  outline: none;
  background-color: white;
}

.file-input-wrapper input[type="file"] {
  font-family: 'Roboto Condensed', sans-serif;
  border-radius: 6px;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
}

.file-input-wrapper input[type="file"]::-webkit-file-upload-button {
  background: #25963f;
  border: none;
  color: white;
  padding: 10px 12px;
  border-radius: 1px;
  cursor: pointer;
  font-weight: bold;
  font-family: 'Roboto Condensed', sans-serif;
  margin-left: -10px;
  margin-top: -15px;
  text-transform: capitalize;
  transition: background 0.3s;
}

.add-employee-form-item input,
.add-employee-form-item select,
.datepicker-input,
.react-datepicker-custom-input {
  font-family: 'Roboto Condensed', sans-serif;
}

.add-employee-form-item input::placeholder,
.datepicker-input::placeholder,
.react-datepicker-custom-input::placeholder {
  font-family: 'Roboto Condensed', sans-serif;
}

.add-employee-form-item select option {
  font-family: 'Roboto Condensed', sans-serif;
}

.shift-select-wrapper select.shift-select {
  padding: 14px 10px;
  font-size: 14px;
  border: 1px solid #cbd5e1;
  border-radius: 1px;
  background-color: #f8fafc;
  transition: border-color 0.2s;
  width: 100%;
  box-sizing: border-box;
  font-family: 'Roboto Condensed', sans-serif;
}

.shift-select-wrapper select.shift-select:focus {
  border-color: #2563eb;
  outline: none;
  background-color: white;
}