.employee-container {
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f8fafc, #e2e8f0);
  font-family: 'Roboto Condensed', sans-serif;
}

.employee-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
}

.employee-title {
  font-size: 1.4rem;
  color: #1e293b;
  font-weight: 600;
  display: flex;
  align-items: center;
}

.employee-add-btn {
  background: linear-gradient(to bottom, #3b82f6, #2563eb);
  color: white;
  padding: 0.8rem 1.2rem;
  border-radius: 1px;
  border: 1px solid #1e40af;
  box-shadow: 0 4px 0 #1e3a8a, 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  transition: all 0.15s ease-in-out;
}

.employee-add-btn:active {
  transform: translateY(2px);
  box-shadow: 0 2px 0 #1e3a8a, 0 2px 4px rgba(0, 0, 0, 0.2);
}

.employee-add-btn:hover {
  background-color: #2563eb;
}

.employee-stats {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 2rem;
}

.employee-stat-card {
  flex: 1 1 200px;
  background: white;
  border-radius: 1px;
  padding: 1rem;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: bold;
  color: #334155;
}

.employee-table-container {
  overflow-x: auto;
}

.employee-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 1px;
  overflow: hidden;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}

.employee-table th,
.employee-table td {
  padding: 0.9rem 1rem;
  text-align: left;
  border-bottom: 1px solid #e2e8f0;
  font-size: 0.95rem;
}

.employee-table th {
  background-color: #cad2da;
  font-weight: 600;
  color: #475569;
}

.employee-table td {
  color: #1e293b;
}

.employee-table tr:hover {
  background-color: #f8fafc;
}

.employee-photo {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid #ccc;
}

.employee-action-buttons {
  display: flex;
  gap: 0.5rem;
}

.employee-edit-btn {
  background-color: #22c55e;
  color: white;
  border: none;
  padding: 0.4rem 0.6rem;
  border-radius: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.employee-edit-btn:hover {
  background-color: #dc2626;
}

.employee-delete-btn {
  background-color: #ef4444;
  color: white;
  border: none;
  padding: 0.4rem 0.6rem;
  border-radius: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.employee-delete-btn:hover {
  background-color: #16a34a;
}

.loading,
.no-data {
  text-align: center;
  padding: 2rem;
  background: white;
  border-radius: 1px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  color: #475569;
  font-weight: 500;
}